body {
  place-items: center;
}

.container-movies {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  grid-gap: 30px;
  gap: 30px;
  padding: 0 50px 0 50px;
  margin-bottom: 60px;
}

.movies__card {
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 240px;
  height: 350px;
  border-radius: 20px;
}

.movies__card img {
  border-radius: 20px;
  width: 100%;
  height: 100%;
  box-shadow: 2px 2px 6px grey;
  transition: all 0.5s ease;
}

.movies__card p {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
}

.movies__description {
  position: absolute;
  height: 350px;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  overflow-y: auto;
  border-radius: 20px;
  transition: all 0.8s ease;
  display: none;
}

.showDescription {
  display: block;
}

.description__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.description__content .content__title {
  font-size: 19px;
}

