.form {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}

.search__container {
  position: relative;
}

.form__input {
  padding: 5px 10px;
  font-size: 18px;
  border-radius: 10px;
  width: 270px;
  height: 35px;
  border: none;
}

.form__input:focus {
  border: none;
  outline: none;
}

.form__button {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  cursor: pointer;
  font-size: 26px;
  border-radius: 10px;
  border: none;
  background: rgb(255, 255, 255);
  display: flex;
  align-items: center;
}

.search__container {
  border-radius: 10px;
}

