body {
  align-items: start;
}

.container__detail {
  display: grid;
  place-items: center;
  height: 60vh;
}

.detail {
  display: grid;
  grid-template-columns: 0.6fr 1fr;
  grid-template-rows: 1fr;
  background: hsl(0, 0%, 100%);
  border-radius: 20px;
  box-shadow: 2px 2px 6px gray;
  width: 760px;
}

.content__title {
  text-align: center;
  font-size: 25px;
  margin-bottom: 8px;
}

.css-1e6y48t-MuiButtonBase-root-MuiButton-root {
  font-size: 0.775rem;
  margin: 10px auto !important;
  padding: 12px 15px !important;
  color: white !important;
  background-color: rgb(0, 0, 0) !important;
  transition: all 0.5s ease !important;
}

.css-1e6y48t-MuiButtonBase-root-MuiButton-root:hover {
  box-shadow: 2px 2px 6px gray;
}

.detail__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px 28px;
}

.content__release_date {
  margin: 0;
}

.content__vote_average {
  text-align: center;
}

.detail__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px 0 0 20px;
}

@media screen and (max-width: 768px) {
  .container__detail {
    align-items: start;
    height: auto;
  }
  .detail {
    grid-template-columns: 1fr;
    grid-template-rows: 360px auto;
    margin: 0 20px;
    width: auto;
  }
  .detail__content {
    padding: 10px 12px;
  }
  .content__title {
    text-align: center;
  }
  .content__description,
  .content__genres {
    font-size: 12px;
  }
  .content__description strong {
    font-size: 13px;
  }
  .detail__img {
    border-radius: 20px 20px 0 0;
  }
  .detail__img img {
    border-radius: 20px 20px 0 0;
    object-fit: unset;
  }
}
